import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { MenuPage } from './menu.page';


const routes: Routes = [
  {
    path: 'menu',
    component: MenuPage,
    children: [
      {
        path: 'restaurant',
        loadChildren: () => import('../restaurant/restaurant.module').then(m => m.RestaurantPageModule)
      },
      {
        path: 'rest-menu',
        loadChildren: () => import('../rest-menu/rest-menu.module').then(m => m.RestMenuPageModule)
      },
      {
        path: 'driver',
        loadChildren: () => import('../driver/driver.module').then(m => m.DriverPageModule)
      },
      {
        path: 'food',
        loadChildren: () => import('../food/food.module').then(m => m.FoodPageModule)
      },
      {
        path: 'account',
        loadChildren: () => import('../account/account.module').then(m => m.AccountPageModule)
      },
      {
        path: 'order',
        loadChildren: () => import('../order/order.module').then(m => m.OrderPageModule)
      },
      {
        path: 'user-role',
        loadChildren: () => import('../user-role/user-role.module').then(m => m.UserRolePageModule)
      },
      {
        path: 'functionality',
        loadChildren: () => import('../functionality/functionality.module').then(m => m.FunctionalityPageModule)
      },
      {
        path: 'food-content',
        loadChildren: () => import('../food-content/food-content.module').then(m => m.FoodContentPageModule)
      },
      {
        path: 'location',
        loadChildren: () => import('../location/location.module').then(m => m.LocationPageModule)
      },
      {
        path: 'vehicle',
        loadChildren: () => import('../vehicle/vehicle.module').then(m => m.VehiclePageModule)
      },
      {
        path: 'details/:id',
        loadChildren: () => import('../details/details.module').then(m => m.DetailsPageModule)
      },
      {
        path: 'home',
        loadChildren: () => import('../home/home.module').then(m => m.HomePageModule)
      },
      {
        path: 'cart-modal',
        loadChildren: () => import('../cart-modal/cart-modal.module').then(m => m.CartModalPageModule)
      },
      {
        path: 'category',
        loadChildren: () => import('../category/category.module').then(m => m.CategoryPageModule)
      },
      {
        path: 'login',
        loadChildren: () => import('../login/login.module').then(m => m.LoginPageModule)
      },
      {
        path: 'order-history',
        loadChildren: () => import('../order-history/order-history.module').then(m => m.OrderHistoryPageModule)
      },
      {
        path: 'driver-home',
        loadChildren: () => import('../driver-home/driver-home.module').then(m => m.DriverHomePageModule)
      },
      {
        path: 'restaurant-home',
        loadChildren: () => import('../restaurant-home/restaurant-home.module').then(m => m.RestaurantHomePageModule)
      },
      {
        path: 'driver-history',
        loadChildren: () => import('../driver-history/driver-history.module').then(m => m.DriverHistoryPageModule)
      },
      {
        path: 'restaurant-history',
        loadChildren: () => import('../restaurant-history/restaurant-history.module').then(m => m.RestaurantHistoryPageModule)
      },
      {
        path: '',
        redirectTo: '/menu/home',
        pathMatch: 'full'
      }
    ]
  },
  {
    path: '',
    redirectTo: '/menu',
    pathMatch: 'full'
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class MenuPageRoutingModule { }
